@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgba(252,236,252,1);
background: -webkit-gradient(left top, right top, color-stop(0%, rgb(233, 103, 233)), color-stop(36%, rgba(253,137,215,1)), color-stop(75%, rgba(251,166,225,1)), color-stop(100%, rgba(255,124,216,1)));
background: -ms-linear-gradient(left, rgba(252,236,252,1) 0%, rgba(253,137,215,1) 36%, rgba(251,166,225,1) 75%, rgba(255,124,216,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

h1{
  font-family: 'Great Vibes', cursive;
  color: fuchsia;
  font-size: 50px;
  text-shadow: 2px 2px 5px purple;
 
}
.box-shadow{
  box-shadow: 0px 8px 20px 0px rgba(243, 88, 210, 0.568);
}

.card:hover{
 background-color: rgb(221, 80, 214);
 color: white;
}
.seachField{
  width:300px;
  outline: purple;
  border: 2px solid rgb(221, 80, 214);
  border-radius: 20px;
  padding-left: 15px;
  height: 40px;
  margin: 0 auto;
  margin:10px 0 10 0 ;

}

